body {
  background-color: #6b9fe8;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

label.logo {
  color: #fff;
  float: left;
  padding-top: 1%;
  padding-left: 1%;
  font-size: 35px;
}

.topnav {
  width: 100%;
  box-sizing: border-box;
  background-color: #444;
  text-decoration: none;
  overflow: hidden;
}

.topnav ul {
  float: right;
}

.topnav ul li {
  text-align: center;
  padding: 14px 16px;
  font-size: 17px;
  display: inline-block;
}

.topnav ul li a {
  color: #2d2d2d;
  background-color: #fff;
  border: 5px solid #fff;
  border-width: 5px 25px;
  border-radius: 25px;
  padding: 10px;
}

.topnav a:hover {
  color: #2d2d2d;
  background-color: #838383;
  border-color: #838383;
}

.topnav a.active {
  font-weight: bold;
}

.topnav .icon {
  display: none;
}

@media screen and (max-width: 600px) {
  .topnav a:not(:first-child) {
    display: none;
  }

  .topnav a.icon {
    float: right;
    background-color: #60786f;
    display: block;
    transform: scale(1.3, 2.43);
  }
}

@media screen and (max-width: 600px) {
  .topnav.responsive {
    position: relative;
  }

  .topnav.responsive .icon {
    position: absolute;
    top: 0;
    right: 0;
  }

  .topnav.responsive a {
    float: none;
    text-align: left;
    display: block;
  }
}

#task-grid {
  grid-template: "taskform tasklist" 1fr
  / 1fr 1fr;
  display: grid;
}

#taskform {
  max-width: 300px;
  grid-area: taskform;
  margin: 0 auto;
}

#taskform label {
  width: 100%;
  display: block;
}

#tasklist {
  grid-area: tasklist;
  list-style-type: none;
}

#mainstopwatch {
  width: 400px;
  height: 50vh;
  text-align: center;
  background: #e0e0e0;
  border-radius: 40px;
  margin: 100px auto;
  padding: 60px 50px 10px;
  font-size: 40px;
  box-shadow: 6px -6px 8px #9d9d9d, -6px 6px 8px #fff;
}

#mainstopwatch span {
  color: #0776de;
  width: 55px;
  background: #e0e0e0;
  margin: 3px;
  padding: 10px;
  display: inline-block;
  box-shadow: 6px -6px 8px #9d9d9d, -6px 6px 8px #fff;
}

#mainstopwatch button {
  -webkit-appearance: none;
  color: #fff;
  width: 110px;
  height: 55px;
  background-color: #2e80b3;
  border: none;
  border-radius: 40px;
  justify-content: space-between;
  font-size: 22px;
  transition: all .2s;
  box-shadow: 6px -6px 8px #9d9d9d, -6px 6px 8px #fff;
}

